.footer {
    position: relative;
	width:100%;	
    z-index: 10;
    padding: 70px 0 70px 0px;
    h4{text-transform: uppercase;}
}
.footer_cont{display:flex; justify-content: space-between;}
.footer_inf{display:flex;
    &_col{width: 25%;}
    ul{
        li{list-style: none;
            a{font-size: 14px;}
        }
    }
    
}
.t-inp_close{display:block; position:absolute; top:50%; right:0; margin-top:-4.5px; width:9px; height:9px; overflow: hidden;
    &:after{position:absolute; top:50%; left:0; transform: rotate(45deg); background: #C4C4C4; content:''; display:block; width:9px; height:1px;}
    &:before{position:absolute; top:50%; left:0; transform: rotate(-45deg); background: #C4C4C4; content:''; display:block; width:9px;  height:1px;}
}
.footer_new{
    .fnew_form{
        &_inp{position:relative; border-bottom: 1px solid #C4C4C4;}
        .t-inp{width:100%; height:40px; border:none; outline: none; color:#333; font-size:14px;  background: transparent;}
        .t-inp::-webkit-input-placeholder {color:rgba(40,40,40,0.6);}
        .t-inp:-moz-placeholder {color:rgba(130,130,130,1);}      
        .t-inp::-moz-placeholder {color:rgba(130,130,130,1);}
        .t-inp:-ms-input-placeholder {color:rgba(130,130,130,1);}  
    }
   .form_submit{background: #FFFFFF; border: 1px solid #333333; width: 150px;  height: 36px; font-size: 14px; text-align: center; line-height: 36px; padding:0;}
}




